@use '../../style/mixins.scss';
@use '../../style/internal/shared_input-select-picker';

/**
 * @prop --notched-outline-border-color: Color of the outlines.
 * @prop --notched-outline-background-color: Color of the background.
 */
$border-radius: 0.25rem;

limel-notched-outline {
    --notched-outline-border-color: #{shared_input-select-picker.$lime-text-field-outline-color};
    --notched-outline-background-color: #{shared_input-select-picker.$background-color-normal};
    display: block;

    *,
    *:before,
    *:after {
        box-sizing: border-box;
    }

    [slot='content'] {
        background-color: var(--notched-outline-background-color);
        border-radius: $border-radius;
    }

    &:hover {
        --notched-outline-border-color: #{shared_input-select-picker.$lime-text-field-outline-color--hovered};
    }
}

.limel-notched-outline {
    // Why is everything prefixed?
    // Because the component has `shadow: false;`
    // and this ensures that we are not inheriting styles.
    pointer-events: none;
    position: absolute;
    bottom: var(--notched-outline-bottom, 0); //FIXME
    inset: 0;

    transition: bottom
        var(--limel-h-l-grid-template-rows-transition-speed, 0.46s)
        cubic-bezier(1, 0.09, 0, 0.89);

    display: flex;
    width: 100%;
    height: 100%;

    &--leading-outline,
    &--notch,
    &--trailing-outline {
        transition: border-color 0.2s ease;
        border-width: 1px;
        border-style: solid;
        border-color: var(--notched-outline-border-color);
    }

    &--leading-outline {
        flex-shrink: 0;
        width: 0.75rem;
        border-right-width: 0;
        border-top-left-radius: $border-radius;
        border-bottom-left-radius: $border-radius;
    }

    &--notch {
        flex-shrink: 0;

        position: relative;
        z-index: 2;

        border-top-width: 0;
        border-right-width: 0;
        border-left-width: 0;

        max-width: calc(100% - 1.5rem);

        label {
            all: unset; // ensures that `label` is not inheriting styles
            @include mixins.truncate-text;

            position: relative;
            transition: color 0.2s ease;
            transform: translateY(-50%);

            display: block;
            padding: 0 0.25rem;

            color: #{shared_input-select-picker.$label-color};
            font-size: 0.65rem; // `10.4px` similar to MDC's floating label
            letter-spacing: var(
                --mdc-typography-subtitle1-letter-spacing,
                0.009375em
            );

            &:after {
                position: absolute;
                right: 0;
                padding: 0 0.25rem;
            }
        }
    }

    &--trailing-outline {
        flex-grow: 1;
        border-left-width: 0;
        border-top-right-radius: $border-radius;
        border-bottom-right-radius: $border-radius;
    }
}

limel-notched-outline[required]:not([required='false']) {
    .limel-notched-outline--notch {
        label {
            padding-right: 0.75rem;

            &:after {
                content: '*';
                scale: 1.5;
            }
        }
    }
}

limel-notched-outline[invalid]:not([invalid='false']) {
    --notched-outline-border-color: var(--lime-error-text-color);

    .limel-notched-outline--notch {
        label {
            &:after {
                color: var(--lime-error-text-color);
            }
        }
    }
}

limel-notched-outline[readonly]:not([readonly='false']) {
    --notched-outline-border-color: transparent !important;
    --notched-outline-background-color: transparent !important;
}
